@import '../../colors.scss';

.container-movies {
    display:grid;
    grid-template: repeat(4,1fr) / repeat(5,1fr);
}

.container-icons {
    display: flex;
    justify-content: center;
    font-size: 30px;
}
.container-icons button {
    margin: 10px 10px;
    background-color: #fff;
    width: 30px;
    height: 30px;
    cursor: pointer;
}